body {
    margin: 0;
    padding: 0;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    font-size: large;
    background-color: black;
}


.margin-pack {
    margin: 2%;
    background: rgb(63,94,251);
    background: -webkit-radial-gradient(circle, rgba(63,94,251,1) 0%, rgba(88,212,212,1) 27%, rgba(252,70,107,1) 100%);
    background: -o-radial-gradient(circle, rgba(63,94,251,1) 0%, rgba(88,212,212,1) 27%, rgba(252,70,107,1) 100%);
    background: radial-gradient(circle, rgba(63,94,251,1) 0%, rgba(88,212,212,1) 27%, rgba(252,70,107,1) 100%);
    border-radius: 5%;
    border-style:groove;
    border-top-color: #16DB93;
    border-bottom-color: #A4036F;
    border-left-color: #EFEA5A;
    border-right-color: #FE5E41;
    border-width: 12px;
}



.margin-tb-pack {
    margin: 3%;
}

.flex-box {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    background: -webkit-gradient(linear, left top, right top, from(rgba(2,0,36,1)), color-stop(35%, rgba(9,9,121,1)), to(rgba(0,212,255,1)));
    background: -webkit-linear-gradient(left, rgba(2,0,36,1) 0%, rgba(9,9,121,1) 35%, rgba(0,212,255,1) 100%);
    background: -o-linear-gradient(left, rgba(2,0,36,1) 0%, rgba(9,9,121,1) 35%, rgba(0,212,255,1) 100%);
    background: linear-gradient(90deg, rgba(2,0,36,1) 0%, rgba(9,9,121,1) 35%, rgba(0,212,255,1) 100%);

}


.pack {
    width: 98%;
    margin: 1% 1% 3%;
}

@media only screen and (min-width: 768px) {
    .pack {
        width: 31%;
        margin: 1%;

    }
} 

.edit {
    background-color: yellow;
    margin-right: 200px;
}

.title {
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    margin-left: 400px;
    background-color: #212121;
  color: #4db1bc;
  margin: 0;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 1fr;
  -ms-flex-align: center;
      align-items: center;
  justify-items: center;
  -webkit-animation: glow 2s ease-in-out infinite;
          animation: glow 2s ease-in-out infinite 
}


